.container {
    width: 100%;
    height: 50vh;
    background-color: #fff;
    padding: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.subContainer {
    padding: 20px;
}

.subContainer h4 {
    text-align: center;
    font-size: 30px;
    line-height: 36px;
}

.formulario {
    display: grid;
    justify-content: center;
    margin: 15px;
}

.formulario input {
    padding: 5px;
    margin-top: 5px;
    margin-bottom: 5px;
}