.container {
    width: 100%;
    height: 85vh;
    display: flex;
    padding-top: 160px;
    padding-bottom: 160px;
    padding-left: 16px;
    padding-right: 16px;
    background-color: #000;
}

.subContainer {
    max-width: 1240px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 28px;
    margin-left: auto;
    margin-right: auto;
}

.cards {
    background-color: #fff;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px;
    margin-top: 40px;
    margin-bottom: 40px;
    border-radius: 8px;
}

.cards h2 {
    padding-bottom: 10px;
}

.cards p {
    padding-top: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid black;
}

.cards a {
    margin-top: 20px;
    padding: 10px;
    padding-inline: 35px;
    background-color: #000;
    border-radius: 5px;
    color: #00df9a;
    text-decoration: none;
    font-size: 15px;
}

.cards:hover {
    -webkit-transform: scale(1.5);
    transform: scale(1.5);
    -webkit-filter: drop-shadow(15px 10px 5px rgba(0, 0, 0, .5));
    filter: drop-shadow(15px 10px 5px rgba(0, 0, 0, .5));
    transition-duration: 400ms;
    border: 1px solid #00df9a;
}

@media (max-width: 768px) {
    .subContainer {
        display: flex;
        flex-direction: column;
    }

    .container {
        width: 100%;
        height: 100%;
        display: flex;
        padding-top: 160px;
        padding-bottom: 160px;
        padding-left: 16px;
        padding-right: 16px;
        background-color: #000;
    }
}