.container {
    color: #fff;
    height: 100%;
    background-color: #000;
}

.subContainer {
    max-width: 100%;
    width: 100%;
    height: 100vh;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 15px;
}

.subContainer h1 {
    font-size: 96px;
    line-height: 40px;
    font-weight: 700px;
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.subContainer div {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px;
}

.subContainer div p {
    font-size: 50px;
    line-height: 28px;
    font-weight: 700px;
    padding-top: 16px;
    padding-bottom: 16px;
}

.paragrafo typed {
    font-size: 20px;
    line-height: 28px;
    font-weight: 700px;
}

.subContainer a {
    background-color: #00df9a;
    width: 200px;
    height: 56px;
    border-radius: 6px;
    font-weight: 500px;
    padding-top: 24px;
    padding-bottom: 24px;
    padding-left: 24px;
    padding-right: 24px;
    color: #000;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    font-size: 20px;
}

@media (max-width: 640px) {
    .subContainer h1 {
        font-size: 50px;
        line-height: 1px;
        padding-top: 24px;
        padding-bottom: 15px;
    }

    .subContainer div p {
        font-size: 48px;
        line-height: 1px;
    }

    .subContainer a {
        margin-top: 50px;
    }


}