.container {
    width: 100%;
    height: 85vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    padding-top: 64px;
    padding-bottom: 64px;
    padding-left: 16px;
    padding-right: 16px;
}

.subContainer {
    max-width: 1240px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-top: 80px;
}

.subContainer img {
    width: 500px;
    margin-top: 16px;
    margin-bottom: 16px;
}

.texto {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.texto h1 {
    font-size: 24px;
    line-height: 32px;
    font-weight: 700px;
    padding-top: 8px;
    padding-bottom: 8px;
}

.texto a {
    background-color: #000;
    width: 200px;
    border-radius: 6px;
    font-weight: 500px;
    margin-top: 24px;
    margin-bottom: 24px;
    padding-left: 24px;
    padding-right: 24px;
    padding-top: 12px;
    padding-bottom: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #00df9a;
    text-decoration: none;
    font-size: 17px;
}

@media (max-width: 768px) {
    .subContainer {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .texto {
        align-items: center;
    }
}