.container_1 {
    width: 100%;
    max-width: 100%;
    height: 96px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0px;
    z-index: 50;
    background-color: #000300;
    color: #fff;
}

.logo {
    width: 100%;
    font-size: 30px;
    line-height: 36px;
    font-weight: 700px;
    color: #00df9a;
    padding-inline: 16px;
}

.menu {
    display: flex;
    list-style: none;
}

.menu li a {
    text-decoration: none;
    padding-inline: 16px;
    color: #fff;
}

.container_2 {
    border: 1px solid #00df9a;
    border-radius: 5px;
    position: fixed;
    left: 0;
    top: 0;
    width: 50%;
    height: 100vh;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 500ms;
    background-color: #000300;
}

.container_2 h1 {
    width: 100%;
    font-size: 30px;
    line-height: 36px;
    font-weight: 700px;
    color: #00df9a;
    padding-top: 30px;
    margin-left: 30px;
}

.container_2 ul {
    display: flex;
    flex-direction: column;
    list-style: none;
    padding: 15px;
}

.container_2 ul li {

    display: flex;
    flex-direction: column;
    list-style: none;
    padding: 15px;
}

.container_2 ul li a {
    text-decoration: none;
    padding-inline: 16px;
    color: #fff;
    border-bottom: 2px solid #fff;
}

.container_2_2 {
    position: fixed;
    left: -100%;
}

.menuHamburguer {
    display: none;
}

@media (max-width: 768px) {
    .menu {
        display: none;
    }

    .menuHamburguer {
        display: block;
    }
}