.container {
    background-color: #000;
    width: 100%;
    padding-top: 64px;
    padding-bottom: 64px;
    padding-left: 16px;
    padding-right: 16px;
    display: grid;
    gap: 32px;
    color: #fff;
}

.subcontainer h1 {

    width: 100%;
    font-size: 30px;
    line-height: 36px;
    font-weight: 700px;
    color: #00df9a;
}

.subcontainer p {
    padding-top: 16px;
    padding-bottom: 16px;
}

.icones {
    display: flex;
    justify-content: space-around;
    margin-top: 24px;
    margin-bottom: 24px;

}

.icones a {
    color: #fff;
}